"use client";

import { useEffect, useState } from "react";
import { useAuth } from "@/lib/AuthContext";
import { supabase } from "@/lib/supabaseClient";
import Link from "next/link";

interface Game {
  id: string;
  title: string;
  image: string;
  price: number;
  purchased_at?: string;
}

export default function LibraryPage() {
  const { user } = useAuth();
  const [library, setLibrary] = useState<Game[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!user) return;

    async function fetchLibrary() {
      setLoading(true);

      try {
        // 1️⃣ Busca os IDs dos jogos comprados pelo usuário
        const { data: purchases, error: purchaseError } = await supabase
          .from("purchases")
          .select("game_id, purchased_at")
          .eq("user_id", user.id);

        if (purchaseError) throw purchaseError;
        if (!purchases || purchases.length === 0) {
          setLibrary([]);
          setLoading(false);
          return;
        }

        const gameIds = purchases.map((p: any) => p.game_id);

        // 2️⃣ Busca os detalhes dos jogos
        const { data: games, error: gameError } = await supabase
          .from("games")
          .select("*")
          .in("id", gameIds);

        if (gameError) throw gameError;

        // 3️⃣ Mapeia a data de compra para cada jogo
        const libraryWithDate = games.map((g: any) => {
          const purchase = purchases.find((p: any) => p.game_id === g.id);
          return { ...g, purchased_at: purchase?.purchased_at };
        });

        setLibrary(libraryWithDate);
      } catch (error) {
        console.error("Erro ao buscar biblioteca:", error);
      }

      setLoading(false);
    }

    fetchLibrary();
  }, [user]);

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-900 text-white">
        <p>Faça login para acessar sua biblioteca.</p>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-900 text-white">
        <p>Carregando biblioteca...</p>
      </div>
    );
  }

  if (library.length === 0) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-gray-900 text-white">
        <p className="mb-4">Você ainda não comprou nenhum jogo.</p>
        <Link href="/" className="bg-blue-600 px-5 py-2 rounded hover:bg-blue-700">
          Explorar Jogos
        </Link>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900 text-white px-6 py-12 max-w-7xl mx-auto">
      <h1 className="text-4xl font-bold mb-8">Minha Biblioteca</h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {library.map((game) => (
          <div key={game.id} className="bg-gray-800 rounded-xl overflow-hidden shadow-lg">
            <img src={game.image} alt={game.title} className="w-full h-48 object-cover" />
            <div className="p-4">
              <h2 className="text-xl font-bold">{game.title}</h2>
              <p className="text-gray-400 text-sm">
                Comprado em: {new Date(game.purchased_at!).toLocaleDateString()}
              </p>
              <p className="text-gray-300 mt-2">Preço: €{game.price.toFixed(2)}</p>
              <Link
                href={`/games/${game.id}`}
                className="mt-4 inline-block bg-blue-600 hover:bg-blue-700 px-4 py-2 rounded font-semibold"
              >
                Jogar
              </Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
